<template>
    <div class="container">
        <h3>ÂåÖ‰æùËµñÂÖ≥Á≥ªÂõæ / Package Dependency Graph</h3>
        <div id="mountNode"></div>
    </div>
</template>

<script>
import G6 from '@antv/g6';
import { mapGetters } from 'vuex';

export default {
    name: "LocalGraph",
    computed: {
        ...mapGetters(['graphData']),
    },
    watch: {
        graphData: {
            handler(newData) {
                this.destroyGraph();
                this.renderGraph(newData);
            },
            deep: false,
        },
    },
    methods: {
        renderGraph(graphData) {
            const container = document.getElementById('mountNode');
            const width = container.scrollWidth;
            const height = container.scrollHeight || 400;

            this.graph = new G6.Graph({
                container: 'mountNode', // ÊåáÂÆöÊåÇËΩΩÂÆπÂô®
                width, // ÂõæÁöÑÂÆΩÂ∫¶
                height, // ÂõæÁöÑÈ´òÂ∫¶
                fitView: true,
                modes: {
                    default: ['zoom-canvas', 'drag-canvas', 'drag-node', 'activate-relations'],
                },
                layout: {
                    type: 'dagre',
                    rankdir: 'LR',
                    align: 'UL',
                    controlPoints: true,
                    nodesepFunc: () => 1,
                    ranksepFunc: () => 1,
                },
                animate: true,
                defaultNode: {
                    // ËäÇÁÇπÂ§ßÂ∞è
                    type: 'rect',
                    size: [280, 30],
                    style: {
                        lineWidth: 2,
                        stroke: '#5B8FF9',
                        fill: '#C6E5FF',
                    },
                    // ËäÇÁÇπ‰∏äÁöÑÊ†áÁ≠æÊñáÊú¨ÈÖçÁΩÆ
                    labelCfg: {
                        // ËäÇÁÇπ‰∏äÁöÑÊ†áÁ≠æÊñáÊú¨Ê†∑ÂºèÈÖçÁΩÆ
                        style: {
                            fill: '#000', // ËäÇÁÇπÊ†áÁ≠æÊñáÂ≠óÈ¢úËâ≤
                            fontSize: 10,
                        },
                    },
                },
                defaultEdge: {
                    type: 'polyline',
                    size: 2,
                    color: '#000',
                    style: {
                        endArrow: {
                            path: 'M 0,0 L 8,4 L 8,-4 Z',
                            fill: '#e2e2e2',
                        },
                        radius: 20,
                    },
                },
            });
            this.graph.data(graphData); // Âä†ËΩΩÊï∞ÊçÆ
            this.graph.render(); // Ê∏≤Êüì
        },
        destroyGraph() {
            if (this.graph) {
                this.graph.destroy(); // ÈîÄÊØÅÂõæÂΩ¢ÂÆû‰æã
                this.graph = null;
            }
        }
    },
    mounted() {
        // ÂàùÂßãÂåñÂõæÂΩ¢
        this.renderGraph(this.graphData);
    }
}
</script>

<style scoped>
.container {
    margin-top: 20px;
}
</style>
